<template>
  <div>
    <Loading v-if="isLoading" :isLoading="isLoading" />
    <div class="about">
      <h1>This is 單一產品頁面</h1>
      {{ product.title }}
    </div>
  </div>
</template>

<script>
import productAPI from '../apis/product';
import Loading from '../components/Loading.vue';

export default {
  name: 'Product',
  data() {
    return {
      product: {},
      isLoading: true,
    };
  },
  created() {
    const { id } = this.$route.params;
    this.getProduct(id);
  },
  components: {
    Loading,
  },
  methods: {
    async getProduct(id) {
      this.isLoading = true;
      const response = await productAPI.getProduct(id);
      const { data } = response;
      if (response.status !== 200) {
        throw new Error('Get Product Fail');
      }
      this.product = data.data;
      this.isLoading = false;
    },
  },
};
</script>
